#site-navigation {
  display: block;
  text-align: right;
  height: 48px;
}


.site-navigation {
  padding: 30px;
  display: block;
  position: absolute;
  text-align: center;
  float: right;
  width: 100vw;
  top: 0;
  background: inherit;
  height: 48px;
  opacity: 1;
  img{
    float: left;
    margin-left: 50px;
    margin-right: 600px;
  }
}

.site-navigation .main-menu,
.site-navigation > input {
  display: none;
}

.site-navigation > label {
  display: block;
  font-size: 36px;
  color: #fff;
  position: absolute;
  right: 20px;
  width: 36px;
  padding: 0;
  cursor: pointer;
  user-select: none;
}

.site-navigation > input:checked + .main-menu {
  display: block;
  clear: both;
  top: 36px;
  position: relative;
  background: #9fcdff;
  color: #fff;
}

.site-navigation ul li {
  display: block;
  padding: 12px 10px;
}
.site-navigation li {
  :hover{
    padding-top: 44px;
    background-color: #9fcdff;
  }
}
.site-navigation a {
  text-decoration: none;
  color: #fff;
}

@media only screen and (min-device-width: 1200px) {
  .site-navigation {
    height: auto;
  }
  .site-navigation .main-menu {
    display: block;
  }
  .site-navigation > label {
    display: none
  }
  .site-navigation ul li {
    display: inline-block;
  }
}